<template>
  <div id="app">
    <div class="header">
      <h1>欢迎来到qy147班管理系统</h1>
      <!-- 添加退出按钮 -->
      <button style="position:absolute;right:40px;top:70px" @click="logout">退出</button>
    </div>
    <div class="left">
      <Left></Left>
    </div>
    <div class="right">
      <!-- 此处就是组件展示的位置 -->
      <!-- router-view 路由的组件 会渲染到这里 路由的出口 -->
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import Left from '../components/Left.vue'
export default {
  data () {
    return {
      name:'aaa'
    }
  },
  methods: {
      //定义退出的方法
      logout(){
          //清除 localStorage中的token信息
          localStorage.removeItem('token')
          //导航到登录页
          this.$router.replace('/login')
      }
  },
  name: 'App',
  components: {
    Left
  },
}
</script>

