<template>
  <div>
       <h3>这是学生的信息展示组件==={{this.$route.params}}</h3>
      <h3>这是学生的信息展示组件==={{id}}==={{name}}</h3>
      <h3>这是学生的信息展示组件==={{this.$route.query}}</h3>
  </div>
</template>

<script>
export default {
    props: ['id','name'],
    mounted () {
        console.log(this)
    },
    beforeRouteUpdate (to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
    console.log('stuinfo的beforeRouteUpdate执行了')
    next()
  },
}
</script>

