<template>
  <div>
    <h1>这里是学生组件</h1>
    <button>
      <router-link active-class="active" to="/index/stu/st/1/aaa">学生一</router-link>
    </button>
    <button>
      <router-link active-class="active" to="/index/stu/st/2/bbb">学生二</router-link>
    </button>
    <button>
      <router-link active-class="active" to="/index/stu/st/3/ccc">学生三</router-link>
    </button>
     <button>
      <router-link active-class="active" 
        :to="{
          path:'/index/stu/st/4/ddd',
          query:{
            address:address,
            age:18
          }
        }"
      >学生四
      </router-link>
    </button>
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      address:'郑州'
    }
  },
  mounted () {
    //console.log(this)
  },
  //进入组件时访问
  beforeRouteEnter (to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
    console.log('beforeRouteEnter',this)
    next()
  },
  //离开组件时访问
  beforeRouteLeave (to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    console.log('beforeRouteLeave',this)
    next()
  }
}
</script>

<style lang='less' scoped>
.active{
  color: blue;
}
</style>