<template>
  <div>
    <ul>
      <li>张老师</li>
      <li>李老师</li>
      <li>Teacher Bai</li>
    </ul>
    <button @click="goStu">点我去学生管理</button>
  </div>
</template>

<script>
export default {
  //$route当前路由对象  $router整个路由器对象
  methods: {
    goStu(){
      //在历史记录栈的顶部 添加一条记录，并且把访问指针指向最新的地址。
      //this.$router.push('/stu')
      //replace就是替换模式，会把记录栈中最新的一条记录给替换掉
      this.$router.replace({path:'/index/stu',query:{
        name:'张三',
        age:18
      }})
    }
  }
}
</script>

