//1.导入vue和vue-router模块
import Vue from 'vue'
import VueRouter from 'vue-router'
//使用路由模块  注册路由模块
Vue.use(VueRouter)
//2.导入我们自己需要的组件 路由组件
import Student from '../views/Student.vue'
//import Subject from '../views/Subject.vue'
import SubList from '../views/SubList.vue'
import TeaList from '../views/TeaList.vue'
import StuInfo from '../views/StuInfo.vue'
import Index from '../views/Index.vue'
import Login from '../views/Login.vue'
//3.配置路由规则 映射关系/对应关系
const routes = [
    //redirect 用来配置重定向 
    //当访问 / 时 会把路径重定向到 stu
    { path: '/', redirect: "login" },
    { path: '/login', component: Login, meta: { title: '登录' } },
    {
        path: '/index',
        component: Index,
        meta: { title: '首页' },
        children: [
            {
                path: 'stu',
                name: 'xuesheng',
                component: Student,
                meta: { st: true, title: '学生信息' },
                children: [
                    //开启路由的props属性的参数传递
                    { path: 'st/:id/:name', component: StuInfo, props: true }
                ],
                //配置独享路由守卫
                beforeEnter(to, from, next) {
                    /* console.log('stu的独享路由守卫被执行了')
                    next() */
                    if(to.meta.st){
                        next()
                    }else{
                        alert('无权访问')
                        next(false)
                    }
                }
            },
            {
                path: 'sub',
                component: () => import('../views/Subject.vue'),
                meta: { st: false, title: '学科信息' },
                children: [
                    /* { path: '/', redirect: "sublist" }, */
                    { path: 'sublist', component: SubList },
                    { path: 'tealist', component: TeaList },
                ]
            },
        ]
    },

]
//4.创建路由器对象,
const router = new VueRouter({
    mode:'history',
    routes
})
//添加配置路由的守卫
router.beforeEach(function (to, from, next) {
    //to :    到哪里去  对象类型  将要访问的路由对象
    //from:   从哪里来  对象类型  离开的路由对象
    //next:   放行      函数类型

    //需求：把stu拦截了。
    /*登录拦截操作
        路由放行的条件，问：什么时候 路由可以放行？
            1. /login 必须放心 如果不放行 我们压根儿没法登录 所以也就没法获取token
            2.有localStorage中 存在 token 信息
            3.设置/index为整个项目的根目录，只要之后的路径访问中 含有/login 就放行。
           
    */
    let token = localStorage.getItem('token');
    //console.log(token)
    if (to.path == '/login') {
        //console.log(to.path)
        //如果token存在 就放行
        if (token) {
            next('/index')
        } else {
            next()
        }
    } else if (to.path.includes('/index')) {
        if (token) {
            next()
        } else {
            next('/login')
        }
    } else {
        //当访问的路径中不包含 /login  /index时 我就默认 你压根没有登录
        next('/login')
    }

})
//定义全局后置路由守卫 没有next()
router.afterEach(function (to, from) {
    console.log('后置守卫执行了')
    document.title = to.meta.title
})

//5.向外暴漏
export default router

